.skill {
    background: rgba(1, 9, 28, 0.75);
    border-radius: 10px;
    margin: 75px;
    padding: 25px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0.25, 0.25));
}
.skill img{
    height: 140px;
    margin: 25px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0.25, 0.25));
}

.skill img:hover{
    transform: scale(1.3); /* Aumenta o tamanho do elemento em 150% */
    transition-duration: 0,5s;
}

.skill h1{
    font-size: 45px;
}

.skill p{
    font-size: 20px;
}