.btn{
    background: transparent;
    border: 2px solid aliceblue;
    color: aliceblue;
    padding: 10px;
    border-radius: 10px;
    font-weight: bolder;
    margin-top: 15px;
    cursor: pointer;
}

.btn:hover{
   color: aliceblue;
   background-color: #010B28;
   transform: translateY(-7px);
   transition-duration: 0,5s;
}